<template>
  <div class="item-card-container" :class="{'remove':inCard}">
    <article>
      <figure class="img-container" @click="getOffer()">
        <b-img class="product-img" :src="`https://offers-jo.easysooq.net/${itemInfo.image_path}`"  fluid></b-img>
        <b-img :src="`https://offers-jo.easysooq.net/${itemInfo.image_path}`" fluid class="s" :class="{'animation-to-card': animateTo}"></b-img>
      </figure>
      <div class="content">
        <h2 @click="getOffer()" >{{itemInfo.name}}</h2>
        <div class="price" @click="getOffer()">
          <span class="old">{{Math.floor(itemInfo.first_offer_price * 1.2)}} دينار </span>
          <span class="new">{{itemInfo.first_offer_price}} دينار</span>
        </div>
        <b-button variant="outline-primary" v-if="!inCard"  @click="addToShoppingCard(itemInfo)">
          <span class="shopping-card" >
            <font icon="shopping-cart"/>
          </span>
          <span >اضف الى السله</span>
        </b-button>
        
        <!--if  in card-->
        <b-button variant="outline-primary" v-if="inCard" @click="removeFromCart(itemInfo.id)">
            <span class="shopping-card">
              <font icon="trash"/>
            </span>
            <span>حذف من السله</span>
        </b-button>
      </div>
    </article>
  </div>
</template>
<script type="text/javascript">
  import {mapActions} from "vuex"
  export default { 
    name:"ItemCard",
    props:['itemInfo', "inCard"],
    data() {
      return {
        animateTo:false,

      }
    },

    methods: {
     ...mapActions(["addToCart",'removeFromCart','getOfferBody']),
     getOffer() {
       this.$bvModal.show('offerModal');
       this.$router.push({
         name:this.$route.name,
         query:{
           productId:this.itemInfo.id
         }
       })
       this.getOfferBody(this.itemInfo.id)
     },
      addToShoppingCard(item) {

        this.removeClasses()
        this.addToCart(item)
      },
      removeClasses() {
        var element = document.getElementById("shopcard");
        var element2 = document.getElementById("shopcard2");
        var vm = this;
        setTimeout(function(){
          element.classList.remove("animated");
          element.classList.remove("bounce");

          element2.classList.remove("animated");
          element2.classList.remove("bounce");
          
          vm.animateTo = false;
        }, 1500);
      },

    },



  }
</script>
